<template>
  <div class="fileBox">
    <!--  -->
    <div class="upload_file">
      
      <input
        class="ipt"
        type="file"
        id="people-file"
        multiple="multiple"
        @change="changefile"
      />
    </div>
    <!--  -->
    <div class="upload_folder">
      
      <input
        class="ipt"
        type="file"
        id="people-folder"
        webkitdirectory=""
        @change="changefile"
      />
    </div>
    <!--  -->
    <div class="filesList">
      <!--  -->
      <totSpeed
        :totalSpeed="totalSpeed"
        :filesListLength="filesList.length"
        :tot="tot"
      >
      </totSpeed>
      <fileList :filesList="filesList"> </fileList>
    </div>
  </div>
</template>
<script>
import totSpeed  from "./totSpeed.vue";
import fileList from "./fileList.vue";
export default {
  data() {
    return {
      filesList: [], //
      tot: 0,        //
    };
  },
  components: {
    totSpeed,
    fileList,
  },

  computed: {
    // 
    totalSpeed() {
      let tot = 0;                        //
      let len = this.filesList.length;    //
      if (len) {
        tot =
          this.filesList.reduce((pre, cur) => {
            return (pre += cur.speed);
          }, tot) / len;
      }
      return tot;
    },
  },
  methods: {
    // 
    changefile(e) {
      this.filesList = [];     //
      let files = e.target.files;
      //
      for (let key of files) {
        let Obj = {
          name: key.name,
          size: key.size,
          date: key.lastModifiedDate,
          speed: 0,
        };
        this.filesList.push(Obj);
        this.uploading(Obj, Obj.speed); //
      }
    },
    // 
    uploading(obj, speed) {
      let interval = Math.floor(Math.random() * 10) + 1; //
      let time = setInterval(() => {
        let num = Math.random();         //
        if (speed <= 100) {
          this.$set(obj, "speed", (speed += num));
        } else {                          //100 100
          this.$set(obj, "speed", 100);
          this.tot++;                      //100
          clearInterval(time);
        }
      }, interval * 10);
    },
  },
};
</script>
<style lang="less" scoped>
.upload_file,
.upload_folder {
  margin: 50px 10px;
  position: relative;
  display: inline-block;
  background: #41a3ff;
  border-radius: 4px;
  padding: 4px 12px;
  overflow: hidden;
  color: #ffffff;
  text-decoration: none;
  text-indent: 0;
  line-height: 20px;
  transition: 0.3s;
  input {
    cursor: pointer;
    position: absolute;
    font-size: 100px;
    right: 0;
    top: 0;
    opacity: 0;
  }
  &:hover {
    background: #003ce2;
    color: #ffffff;
    text-decoration: none;
  }
}
.filesList {
  width: 90%;
  height: 700px;
  margin: 0 auto;
  padding: 5px;
  box-sizing: border-box;
  overflow: hidden;
  overflow-y: auto;
}
</style>
